@import "main.scss";


body {
    padding: 0px 0 20px 0px;
    margin: 0px;
    font: 14px/1.5 'Open Sans', sans-serif;
    color: #A3BFC6 ;
    font-weight: normal;
    -webkit-font-smoothing: subpixel-antialiased;
  }

.circular {
    width: 250px;
    height: 250px;
    border-radius: 150px;
    -webkit-border-radius: 150px;
    -moz-border-radius: 150px;
    margin-bottom: 5px;
    margin-top: 15px;
  }

.rectangular {
    width: 300px;
    height: 250px;
    margin-top: 15px;
    margin-bottom: 5px;
}

h1 {
    font-size: 45px;
    line-height: 0.5;
}

.navigation {
  list-style-type: hidden;
  margin: 0;
  padding: 0;
  overflow: hidden;
  
  
}

.navigation a {
  
  color: rgb(85, 117, 126);
  
  padding: 14px 16px;
  text-decoration: none;
}

.navigation a:hover {
  background-color:rgb(214, 214, 14);
}

.footer {
  color: #A3BFC6;
  text-align: center;
  margin: 2rem;
}

.footer a {
  color: #A3BFC6;
  padding: 10px;
  text-decoration: none;
}

.footer a:hover {
  color: rgb(214, 214, 14);
}



